
body {
  font-family: 'Kavivanar', cursive;
  background: rgba(41, 39, 39, 1);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(41, 39, 39, 0.4)), to(rgba(41, 39, 39, 0.4))), url(/static/img/wall.a6eaa1a.jpg) no-repeat fixed center;
  background: linear-gradient(rgba(41, 39, 39, 0.4), rgba(41, 39, 39, 0.4)), url(/static/img/wall.a6eaa1a.jpg) no-repeat fixed center;
  background-size: cover;
  color: #f2f2f2;
}
.alert {
  margin-top: 10px;
}
.modal-content {
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(80, 80, 80)), to(rgb(51, 51, 51)));
  background: linear-gradient(rgb(80, 80, 80), rgb(51, 51, 51));
}
.navbar, .navbar-inverse {
