}
.dashboard-row {
  min-height: 35vh;
}
.dashboard-main {
  min-height: 30vh;
}
.empty-message {
  margin-bottom: 30px;
}
.search-bar {
  margin-bottom: 40px;
}
.keep, .vault {
  border: 1px solid #404040;
  background: #404040;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(80, 80, 80)), to(rgb(51, 51, 51)));
  background: linear-gradient(rgb(80, 80, 80), rgb(51, 51, 51));
}
.vault-title {
  margin: 10px 0;
}
.vault-description {
  margin: 30px 0;
}
.main-title {
  font-size: 4em;
  margin: 40px 0 40px;
}
.secondary-title {
  font-size: 3em;
  margin: 40px 0 40px;
}
.main-title,
.secondary-title,
.empty-message {
  text-shadow: 0px 3px #000;
}
.btn {
  border: none;
}
.btn-primary {
