}
.btn-alt:hover,
.btn-alt:active,
.btn-alt:focus {
  background: #e6b800;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(230, 184, 0, 0.6)), to(rgba(230, 184, 0, 0.6))), url(/static/img/gem.3d46f45.jpg) no-repeat center;
  background: linear-gradient(rgba(230, 184, 0, 0.6), rgba(230, 184, 0, 0.6)), url(/static/img/gem.3d46f45.jpg) no-repeat center;
  color: #000;
}
.btn-alt-2:hover,
.btn-alt-2:active,
.btn-alt-2:focus {
  background: #00e6e6;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 230, 230, 0.6)), to(rgba(0, 230, 230, 0.6))), url(/static/img/gem.3d46f45.jpg) no-repeat center;
  background: linear-gradient(rgba(0, 230, 230, 0.6), rgba(0, 230, 230, 0.6)), url(/static/img/gem.3d46f45.jpg) no-repeat center;
  color: #000;
}
.btn-alt-3:hover,
.btn-alt-3:active,
.btn-alt-3:focus {
  background: #862d86;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(134, 45, 134, 0.6)), to(rgba(134, 45, 134, 0.6))), url(/static/img/gem.3d46f45.jpg) no-repeat center;
  background: linear-gradient(rgba(134, 45, 134, 0.6), rgba(134, 45, 134, 0.6)), url(/static/img/gem.3d46f45.jpg) no-repeat center;
  color: #fff;
}
.btn-modal {
  background: #737373;
  color: #fff;
}
.btn-modal:hover,
.btn-modal:active,
.btn-modal:focus {
  background: #666666;
  color: #fff;
}
.image-wrapper {
  position: relative;
  width: 100%;
}
.image-wrapper:hover .keep-image {
  opacity: 0.3;
}
.image-wrapper:hover .overlay-content {
  opacity: 1;
}
.keep-image {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  -webkit-transition: .5s ease;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 1%;
  border: 1px solid #404040;
  margin-bottom: 15px;
}
.keep-large-view {
  margin-bottom: 20px;
  max-width: 500px;
}
.overlay-content {
  -webkit-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}
.glyphicon {
  font-size: 1.5em;
}
.small-icon {
  font-size: 0.9em;
}
.custom-icon,
.counter-label {
  font-size: 1.4em;
}
.counter {
  margin: 0 5px;
}
.btn-icon {
  width: 45px;
  height: 40px;
  border-radius: 20%;
}
.add-button {
  width: 42px;
  height: 40px;
  border-radius: 100%;
}
.main-font {
  font-family: 'Modern Antiqua', cursive;
}
.btn-default {
  width: 100%;
}
.navbar ul {
  list-style-type: none;
}
.right-btn {
  margin: 0 10px 0 2px;
}
@media (max-width: 2000px) {
.navbar-header {
    float: none;
}
.navbar-toggle {
    display: block;
}
.navbar-nav {
    float: none!important;
    margin: 7.5px -15px;
}
.navbar-nav>li {
    float: none;
}
.navbar-nav>li>a {
    padding-top: 10px;
    padding-bottom: 10px;
}
.state {
    width: 50%
}
}


























































































































































































